import { Injectable } from '@angular/core';
import { Router, CanActivate } from '@angular/router';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { Platform } from '@ionic/angular';

import { environment } from 'src/environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService implements CanActivate {

  authState = new BehaviorSubject(false);

  constructor(
    private http: HttpClient,
    private platform: Platform,
    private router: Router,
  ) {

    // this.platform.ready().then(() => {
    //   this.ifLoggedIn();
    // });

  }

  canActivate(): boolean {
    let currentUser = localStorage.getItem('currentUser');

    try {
      if (currentUser !== "undefined" && currentUser != "") {
        let d = JSON.parse(currentUser);
        if (d.api_key != '') {
          return true
        }
      }

      this.router.navigate(['/front']);
      return false;

    } catch (err) {
      this.router.navigate(['/front']);
      return false;
    }


  }


  setHeader() {
    let currentUser = localStorage.getItem('currentUser');

    var headerOption = {
      'accept-language': 'id',
      'Content-Type': 'application/x-www-form-urlencoded',
    }

    if (currentUser != null && currentUser !== "undefined") {
      let d = JSON.parse(currentUser);
      if (d.api_key != '') {
        headerOption['x-api-key'] = d.api_key;
      }
    }

    return new HttpHeaders(headerOption);
  }



  loginUser(username, password) {

    let body = "email=" + username + "&password=" + password;

    return this.http.post<any>(`${environment.apiUrl + 'users/login'}`, body,
      { headers: this.setHeader() })
      .pipe(map(data => {
        localStorage.setItem('tutupAlertPetunjuk', '0');
        localStorage.setItem('currentUser', JSON.stringify(data));
        localStorage.setItem('geo_id', data.user.geo_id);
        localStorage.setItem('api_key', data.api_key);
        this.authState.next(true);
        return data;
      }));

  }

  logoutUser() {

    return this.http.post<any>(environment.apiUrl + 'users/logout'
      , {},
      { headers: this.setHeader() })
      .pipe(map(data => {

        localStorage.removeItem('currentUser');
        localStorage.removeItem('geo_id');
        localStorage.removeItem('api_key');
        this.authState.next(null);
        this.router.navigate(['front/']);
        return true;

      }));

  }

}
