import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthenticationService } from './_services/authentication.service';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'front',
    loadChildren: () => import('./pages/front/front.module').then(m => m.FrontPageModule)
  },
  {
    path: 'home',
    loadChildren: () => import('./pages/home/home.module').then(m => m.HomePageModule),
    canActivate: [AuthenticationService]

  },
  {
    path: 'laporan',
    loadChildren: () => import('./pages/laporan/laporan.module').then( m => m.LaporanPageModule)
  },
  {
    path: 'basicdata',
    loadChildren: () => import('./pages/basicdata/basicdata.module').then( m => m.BasicdataPageModule)
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
